// Basic tags

body {
	background-color: rgb(244,244,244);
	margin-top: 128px;
	height: 100%;
}

h1, h2 {
	margin-top: 0;
}

p {
	margin-bottom: 3px;
}

a {
	text-decoration: none;
}

hr {
	margin-top: .5em;
	margin-bottom: .5em;
}

textarea.form-control {
	height: 10em;
	max-width: 522px;
}

footer {
	clear: both;
	text-align: center;
	margin-bottom: 2em;
}

// Card class

.card {
	max-width: 580px;
	margin-left: auto;
	margin-right: auto;
	margin-top: 2em;
	margin-bottom: 2em;
	border-style: solid;
	border-color: rgb(192,192,192);
	border-width: 1px;
	padding: 2em;
	background-color: #ffffff;
}

// Navbar classes

$navbar-backgroundColor: rgb(168,0,168);
$navbar-textColor: #ffffff;

.navbar {
	background-color: $navbar-backgroundColor;
	border-color: $navbar-backgroundColor;
	margin: 0;
}

.navbar-inverse a {
	color: $navbar-textColor !important;
	margin-right: 12px;
}

.navbar-form {
	margin-top: 8px;
	margin-bottom: 8px;
	padding-right: 15px;
	padding-left: 15px;
	border: 0;
}

.navbar-inverse span {
	color: $navbar-textColor !important;
}

.navbar form {
	padding-right: 12px;
	display: inline;
}

// Toolbar class

$toolbar-backgroundColor: rgb(45,176,123);

.toolbar {
	top: 51px;
	background-color: $toolbar-backgroundColor;
	border-color: $toolbar-backgroundColor;
	margin: 0;
}

// Label class

.label {
	margin-left: 5px;
	margin-bottom: 5px;
}

// Dropdown button menu class

$dropdown-color: rgb(114,201,226);

.dropdown-menu {
	background-color: $dropdown-color;
}

// Count class

.count {
	color: $navbar-textColor !important;
	margin-left: 12px;
}

// Media classes

.media-object, .media-object-form {
	max-width: 256px;
	padding-right: 5px;
}

.media-object-small {
	max-width: 75px;
	padding-top: 2px;
}

.media-left {
	max-width: 266px;
	padding-right: 15px;
}

// Delete button class

.delete-button {
	margin-bottom: 0 !important;
	margin-left: 5px;
}

// Star icon class

$star-color: rgb(255,242,0);

.star-icon {
	color: $star-color !important;
	font-size: 18px;
}

// Search icon class

.search-icon {
	font-size: 20px;
}

// Media queries - responsive design

@media (max-width: 768px) {

	.navbar {
		max-height: 102px;
	}

	.navbar-left, .navbar-right {
		margin: 0;
	}

	.navbar-form {
		margin: auto;
		padding-top: 8px;
		padding-bottom: 8px;
		padding-right: 15px;
		padding-left: 15px;
		border: 0;
	}

	.navbar-toggle {
		border-color: $navbar-textColor !important;
	}

	.navbar-collapse {
		border-color: $navbar-textColor !important;
		padding: 0;
	}

	.navbar-inverse a {
		margin-right: 5px;
	}

	.navbar form {
		padding-right: 5px;
	}

	.dropdown-menu {
		background-color: $dropdown-color !important;
	}

	.media-object, .media-object-form {
		max-width: 75px;
		padding-top: 2px;
	}

}